import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import ImgUpload from '../../ImgUpload'

const CreateUser = () => {
    const history = useHistory()
    const addressRef = useRef()
    const [imgUpLoading, setimgUpLoading] = useState(false)
    const [fileUrls, setFileUrls] = useState([])
    const [state, setState] = useState({
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        fileList: []
    })

    const [isLoading, setLoading] = useState(false)
    const [postcode, setPostCode] = useState('')
    const [searching, setSearching] = useState(false)
    const [ukaddress, setukaddress] = useState([])
    const [userInput, setUserInput] = useState({
        mobile: '',
        email: '',
        full_name: '',
        address: ''
    })
    const handlePostCodeSearch = e => {
        e.preventDefault()
        setSearching(true)
        useJwt.ukAddressEndpoint({ postCode: postcode }).then(res => {
            console.log(res.data.payload)
            setukaddress(res.data.payload.addresses.map(element => `${element.building_number}, ${element.thoroughfare}, ${element.locality}, ${element.town_or_city}`))
            setSearching(false)
        }).catch(err => {
            setukaddress([])
            setSearching(false)
            Error(err)
        })
    }
    const [error, seterror] = useState({
        email: false,
        mobile: false
    })
    const handleChange = e => {
        const chkEmail = /\S+@\S+\.\S+/.test(e.target.value)
        // Email Validator
        if (e.target.name === 'email' && chkEmail) {
            seterror({ ...error, email: false })
        }
        if (e.target.name === 'email' && !chkEmail) {
            seterror({ ...error, email: true })
        }
        //mobile number validator
        if (e.target.name === "mobile") {
            // console.log(e.target.value)
            // if (e.target.value[0] === '0') {
            //     seterror({ ...error, mobile: true })
            //     return 0
            // } else {
            //     seterror({ ...error, mobile: false })
            // }
            if (!/^[0-9]*$/.test(e.target.value)) {
                return 0
            }
        }
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    const handleSubmit = e => {
        e.preventDefault()
        if (imgUpLoading) {
            toast.error('please wait, image is uploading to the server')
            return 0
        }
        const photo = fileUrls.length ? fileUrls[0].url : null
        console.log(userInput)
        const { mobile, email, full_name, address } = userInput
        const chkEmail = /\S+@\S+\.\S+/.test(email)
        if (!chkEmail) {
            return 0
        }
        setLoading(true)
        useJwt.createSuperAdminUsers({ mobile: mobile.length === 11 ? mobile.substring(1) : mobile, email, full_name, address, photo }).then(res => {
            console.log(res)
            setLoading(false)
            history.push('/SuperAdmin/AllUsers')
        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }
    return (
        <Fragment>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Create New User</CardTitle>
                </CardHeader>
                <CardBody>
                    <Form className="row pt-1" style={{ width: '100%' }} onSubmit={handleSubmit} autoComplete="off">
                        <Col md='4' >
                            <FormGroup>
                                <Label for="full_name">Full Name</Label>
                                <Input
                                    required
                                    type="text"
                                    id='full_name'
                                    name="full_name"
                                    placeholder="manager name"
                                    value={userInput.full_name}
                                    onChange={handleChange}
                                />
                            </FormGroup>
                        </Col>
                        <Col md='4' >
                            <FormGroup>
                                <Label className='form-label' for='mobile'>
                                    Mobile Number
                                </Label>
                                <InputGroup className='input-group-merge w-100 mb-1'>
                                    <InputGroupAddon addonType='prepend'>
                                        <InputGroupText>
                                            {PHONE_PREFIX}
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        autoFocus
                                        required
                                        type='text'
                                        minLength={String(userInput.mobile)[0] === '0' ? 11 : 10}
                                        maxLength={String(userInput.mobile)[0] === '0' ? 11 : 10}
                                        id='mobile'
                                        name='mobile'
                                        value={userInput.mobile}
                                        onChange={handleChange}
                                        placeholder='123-456-7899'
                                    />
                                </InputGroup>
                                {/* {error.mobile && <span style={{ color: 'red', fontSize: '11px' }}>First digit 0 not allow</span>} */}
                            </FormGroup>
                        </Col>
                        <Col md='4' >
                            <FormGroup>
                                <Label for="email">Email</Label>
                                <Input
                                    required
                                    type="email"
                                    id='email'
                                    name="email"
                                    placeholder="example@gmail.com"
                                    value={userInput.email}
                                    onChange={handleChange}
                                />
                                {error.email && userInput.email && <span style={{ color: 'red', fontSize: '11px' }}>Please Enter a valid Email</span>}
                            </FormGroup>
                        </Col>

                        <Col md='6' >
                            <Label for="postcode">Search Address by Post Code</Label>
                            <InputGroup>
                                <Input
                                    // required
                                    type="text"
                                    id='postcode'
                                    name="postcode"
                                    placeholder="enter a valid postcode"
                                    value={postcode}
                                    onChange={e => setPostCode(e.target.value)}
                                />
                                <InputGroupAddon addonType='append'>
                                    <Button color='primary' outline type='button' onClick={handlePostCodeSearch}>
                                        {searching ? <Spinner size='sm' /> : 'Search'}
                                    </Button>
                                </InputGroupAddon>
                            </InputGroup>
                        </Col>
                        <Col md='6' >
                            <Label>Address</Label>
                            <><Select
                                ref={addressRef}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                name='address'
                                onChange={selected => {
                                    setUserInput({ ...userInput, address: selected.value })
                                }}
                                maxMenuHeight={150}
                                options={ukaddress.map(x => { return { value: x, label: x } })}
                                value={{ value: userInput.address, label: userInput.address || 'Select' }}
                                // isDisabled={!ukaddress.length}
                                isClearable={false}
                            /><Input
                                    required
                                    style={{
                                        opacity: 0,
                                        width: "100%",
                                        height: 0
                                        // position: "absolute"
                                    }}
                                    onFocus={e => addressRef.current.select.focus()}
                                    value={userInput.address || ''}
                                    onChange={e => ''}
                                /></>
                        </Col>
                        <Col md='12' className='my-0'>
                            <Label>Photo</Label>
                            <ImgUpload
                                setimgUpLoading={setimgUpLoading}
                                state={state}
                                setState={setState}
                                fileUrls={fileUrls}
                                setFileUrls={setFileUrls} />
                        </Col>
                        <Col md="12 text-center">
                            <Button.Ripple className='mt-2' color='primary' type="submit" disabled={isLoading}>
                                {isLoading ? <Spinner size='sm' /> : 'Submit'}
                            </Button.Ripple>
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateUser